import React,{useState,useEffect} from 'react'
import { Link,useNavigate } from 'react-router-dom'
const Header = () => {

const [loginUser, setloginUser] = useState('')
useEffect(() => {
  const user=JSON.parse(localStorage.getItem('user'))
  if(user){
    setloginUser(user)
  }
}, [])
const navigate=useNavigate()
const logoutHandler = () =>{
localStorage.removeItem("user")
navigate("/login")
}

  return (
    <div>
   <nav className="navbar navbar-expand-lg bg-body-tertiary">
  <div className="container-fluid">
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarTogglerDemo01" aria-controls="navbarTogglerDemo01" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon" />
    </button>
    <div className="collapse navbar-collapse" id="navbarTogglerDemo01">
      <Link className="navbar-brand" href="#">AUTHENTICATION SYSTEM</Link>
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
      <li className="nav-item btn btn-danger">
      <Link className="nav-link  btn btn-danger">{loginUser && loginUser.name}</Link>
    </li>
    
  </ul>
    </div>
  </div>
  <button className=" container-fluid nav-item btn btn-primary" onClick={logoutHandler}>
    Logout
  </button>
</nav>

    </div>
  )
}

export default Header
